/* CSS of MBC pricing */
/* toggle design */
.ced-switch-wrapper {
  position: relative;
  display: inline-flex;
  padding: 4px;
  text-align: center;
  border: 1px solid lightgrey;
  margin-bottom: 40px;
  border-radius: 30px;
  background: #fff;
}

.ced-pricing-container-common {
  margin: 0 auto;
  text-align: center;
  margin-top: 40px;
}

.ced-switch-wrapper [type="radio"] {
  position: absolute;
  left: -9999px;
}

.ced-switch-wrapper [type="radio"]:checked#monthly ~ label[htmlFor="monthly"],
.ced-switch-wrapper [type="radio"]:checked#yearly ~ label[htmlFor="yearly"] {
  color: #fff;
}

.ced-switch-wrapper [type="radio"]:checked#monthly ~ label[htmlFor="monthly"]:hover,
.ced-switch-wrapper [type="radio"]:checked#yearly ~ label[htmlFor="yearly"]:hover {
  background: transparent;
}

.ced-switch-wrapper [type="radio"]:checked#monthly + label[htmlFor="yearly"] ~ .highlighter {
  transform: none;
}

.ced-switch-wrapper [type="radio"]:checked#yearly + label[htmlFor="monthly"] ~ .highlighter {
  transform: translateX(100%);
}

.ced-switch-wrapper label {
  font-size: 16px;
  z-index: 1;
  min-width: 100px;
  line-height: 32px;
  cursor: pointer;
  border-radius: 30px;
  transition: color 0.25s ease-in-out;
}

.ced-switch-wrapper label:hover {
  background: #eee;
}

.ced-switch-wrapper .highlighter {
  position: absolute;
  top: 4px;
  left: 4px;
  width: calc(50% - 4px);
  height: calc(100% - 8px);
  border-radius: 30px;
  background: #815FE8;
  transition: transform 0.25s ease-in-out;
}

/* toggle styling */

/* select-marketplace styling */
