/* update pricing page */

.ced-pricing-plan-wrap {
	width: 40%;
	margin: auto;
	padding: 30px;
	transform: translateY(30%);
	background-position: bottom;
  background-image: url("back.png");
	border-radius: 5px;
	border: 1px solid black;
	margin-bottom: 20%;
}
.ced-pricing-action-buttons .ced-update {
	margin-bottom: 20px;
}
.ced-pricing-plan-common-wrapper {
	border-bottom: 1px solid #ccc;
	margin-bottom: 20px;
}
.ced-pricing-plan-common-wrapper h2 {
	margin: 0;
	margin-bottom: 20px;
}
.ced-pricing-plan-details-wrap {
	width: 70%;
}
.ced-pricing-plan-details-wrap table {
	width: 100%;
}
.ced-pricing-plan-details-wrap table tbody tr td {
	width: 60%;
	padding: 6px 0;
}
.ced-pricing-plan-action-container {
	text-align: center;
}
.ced-pricing-plan-details-container {
	display: flex;
	align-items: center;
}
.ced-active::before {
	position: absolute;
	content: '';
	height: 13px;
	width: 13px;
	background: #10ea10;
	border-radius: 50%;
	left: -16px;
	top: 4px;
}
.ced-active {
	position: relative;
	content: '';
	margin-left: 14px;
}
.ced-pricing-plan-action-container::after {
	position: absolute;
	top: -35px;
	left: 14px;
	bottom: 0;
	width: 2px;
	height: 170px;
	background: #ccc;
	content: '';
}
.ced-pricing-plan-action-container {
	position: relative;
	content: '';
	width: 30%;
	text-align: center;
}

.ced-pricing-action-buttons a {
	display: block;
	margin-bottom: 20px;
}
.ced-update {
	background: #815FE8;
	border: none;
	padding: 10px 20px;
	border-radius: 4px;
	color: #fff;
	cursor: pointer;
}
.ced-cancel {
	background: #b32d2e;
	border: none;
	padding: 10px 20px;
	border-radius: 5px;
	color: #fff;
	cursor: pointer;
}
/* Free plan Popup */
.popup-content {
	background-color: #fff;
	padding: 20px;
	border-radius: 5px;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
	text-align: center;
}

.ced-popup-overlay {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: rgba(0, 0, 0, 0.7);
	visibility: visible;
	opacity: 1;
}

.ced-popup-wrap {
	margin: 15% auto;
	padding: 20px;
	background: #fff;
	border-radius: 5px;
	width: 30%;
	position: relative;
}

.ced-popup-wrap h2 {
	margin-top: 0;
	color: #333;
	text-align: center;
}
.ced-popup-close {
	cursor: pointer;
}
.ced-popup-wrap .ced-popup-content-wrapper {
	max-height: 30%;
	overflow: auto;
}

.ced-form-wrapper-popup input {
	display: block;
	padding: 10px;
	margin: 0 auto;
	border-radius: 6px;
	border: 1px solid #ccc;
	margin-bottom: 12px;
	width: 89%;
}

.ced-popup-fomr-steps {
	display: flex;
	justify-content: center;
	padding-top: 10px;
	font-size: 12px;
}

.ced-popup-fomr-steps a {
	text-decoration: none;
	color: #000;
}

.ced-popup-form-button {
	background: #0071A1;
	width: 100%;
	min-width: 90%;
	border: none !important;
	color: #fff;
	font-weight: bold;
	padding: 10px !important;
	cursor: pointer;
}

.ced-popup-content-wrapper p {
	font-size: 13px;
	margin-bottom: 30px;
	width: 90%;
	margin: 0 auto 30px auto;
}

.blur-overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(255, 255, 255, 0.5); /* semi-transparent background */
	backdrop-filter: blur(5px); /* blur effect */
	z-index: 1000; /* Ensure it covers other content */
	display: flex;
	align-items: center;
	justify-content: center;
  }
  